module.exports = function convertGradeToGpa(schoolSettings, gradeType, grade) {
  if (gradeType === 'gpa') {
    return +grade;
  }

  if (gradeType === 'percent') {
    grade = +grade;
    var gradeConversions = schoolSettings.gradeConversions.sort(function (a, b) {
      return b.percent - a.percent;
    });
    return (gradeConversions.find(function (row) {
      return row.percent <= grade;
    }) || last(gradeConversions)).gpa;
  }

  if (gradeType === 'letter') {
    var row = schoolSettings.gradeConversions.find(function (row) {
      return row.letter === grade;
    });
    if (!row) throw new Error("Invalid grade letter ".concat(grade));
    return row.gpa;
  }

  throw new Error("Invalid grade type ".concat(gradeType));
};

function last(array) {
  return array[array.length - 1];
}